

































































































import Vue from 'vue'
import Component from 'vue-class-component'
import * as store from '../plugins/store'
import { i18n } from '../plugins/i18n'
import * as api from '../utils/api'
import Variant from './Variant.vue'

@Component({
  props: {
    localization: Object,
    languages: Array,
    loadData: Function,
    admin: Boolean,
  },
  components: {
    Variant,
  },
})
export default class LocalizationCard extends Vue {
  text = ''
  language = ''

  edit = false
  select = false

  textRules = [(v: any) => !!(v || '').trim() || i18n.t('errors.textLength')]
  languageRules = [(v: any) => !!(v || '').trim() || i18n.t('errors.language')]

  loading = false

  selected = {} as any

  async save(key: string) {
    this.loading = true
    try {
      await api.postVariant(key, this.text, this.language, store.username())
      this.$props.loadData()
      this.text = ''
      this.edit = false
    } catch (err) {
      store.setSnackbarError(err.response.data)
    } finally {
      this.loading = false
    }
  }

  async deleteLocalization(key: string) {
    this.loading = true
    try {
      await api.deleteLocalization(key)
      this.$props.loadData()
    } catch (err) {
      store.setSnackbarError(err.response.data)
    } finally {
      this.loading = false
    }
  }

  async deleteVariants(key: string) {
    this.loading = true
    try {
      await api.deleteVariants(
        key,
        Object.keys(this.selected).filter((k) => !!this.selected[k])
      )
      this.selected = {}
      this.edit = false
      this.$props.loadData()
    } catch (err) {
      store.setSnackbarError(err.response.data)
    } finally {
      this.loading = false
    }
  }

  async selectVariants(key: string) {
    this.loading = true
    try {
      await api.selectVariants(
        key,
        Object.keys(this.selected).filter((k) => !!this.selected[k])
      )
      this.selected = {}
      this.edit = false
      this.$props.loadData()
    } catch (err) {
      store.setSnackbarError(err.response.data)
    } finally {
      this.loading = false
    }
  }
}
